/** @format */

.App {
    text-align: center;
}

.page-title {
    color: white;
    font-size: 20px;
    font-weight: bolder;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    text-transform: uppercase;
    vertical-align: middle;
    margin: 20px 10px 10px;
    border-style: solid;
    border-width: 1px;
    background-color: grey;
    padding: 5px;
    border-color: black;
}

.complete-row {
    color: red;
    font-weight: bolder;
}

.no-data-message {
    font-size: 25px;
    text-align: center;
    text-decoration: underline;
    font-weight: bold;
}

.restrict-card {
    height: 175px;

    background-color: blue;
}

.react-excel .active-sheet {
    background-color: #a1d6e2;
    color: rgb(65, 65, 66);
}

.react-excel div {
    display: flex;
    justify-content: space-evenly;
    margin-top: 2rem;
}

.react-excel div button {
    border: 1px solid black;
    border-radius: 10px;
    padding: 1rem 2rem;
    color: rgb(86, 86, 231);
    cursor: pointer;
    font-size: larger;
}

.react-excel table {
    margin: 2rem auto;
    border-collapse: collapse;
}

.react-excel th,
.react-excel td {
    border: 1px solid #ddd;
    padding: 8px;
}

.react-excel table tr:nth-child(even) {
    background-color: #b4b4f8;
}

.custom-ui {
    background: rgb(26, 0, 26);
    width: 450px;
    height: 180px;
    text-align: center;
    color: white;
}

.custom-ui button {
    background-color: transparent;
    border: 1px solid white;
    width: 150px;
    height: 2rem;
    margin: 1rem;
    color: white;
}

.grid-bold-font {
    font-weight: bold;
}

.modal-styles {
    background-color: deeppink;
}

.cell-pass {
    color: darkgreen;
    font-weight: bold;
}

.cell-warning {
    color: red;
    font-weight: bold;
}

.Modal {
    position: absolute;
    top: 40px;
    left: 40px;
    right: 40px;
    bottom: 40px;
    background-color: #f2f2f2;
    padding: 20px;
    border: 10px solid #ccc;
    border-radius: 10px;
}

.Overlay {
    position: fixed;
    top: 75px;
    left: 250px;
    right: 100px;
    bottom: 75px;
    background-color: #22415e;
    border-radius: 10px;
}

.SmallOverlay {
    position: fixed;
    left: 75em;
    width: 500px;
    background-color: #22415e;
    border-radius: 0.5em;
}

.button-footer {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
}

.accounting-total {
    border-bottom: 1px solid;
    text-decoration: overline underline;
}

.accounting-double {
    border-bottom: 1px solid;
    text-decoration: underline;
}

.accounting-single {
    text-decoration: underline;
}

.remove-cell-padding {
    padding-left: 2px !important;
    padding-right: 2px !important;
}

